@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*{
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Open Sans', sans-serif;
}

.app{
  background-size: cover;
  background-position: center center;
  transition: all 0.4s ease-in-out;
}

.gradient-background{
  background-image: linear-gradient(to bottom, rgba(0,0,0, 0.1), rgba(0,0,0, 0.5));
  min-height: 100vh;
}

.weather-wrapper{
  width: 100%;
  max-width: 344px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.btn-city{
  background-color: white;
  padding: 5px;
  text-align: center;
  border: 1px solid #eee;
  border-radius: 10px;
  margin: 5px;
  cursor: pointer;
  outline: none;
  width: 76px;
}

.btn-city img{
  display: block;
}

.weather-container{
  background-color: white;
  border-radius: 15px;
  padding: 15px;
  text-align: center;
  margin: 5px;
}

.date{
  font-size: 14px;
}

.city-name{
  font-size: 30px;
}

.degrees{
  font-size: 62px;
}

.weather-name{
  font-size: 18px;
}

